.control-panel {
    position: absolute;
    z-index: 100;
    font-size: 20px;
    padding: 10px;
    color: whitesmoke;
    bottom: 0;
    margin: auto;
}

.control-panel-left {
    left: 0;
    text-align: left;
    border-right: 1px solid forestgreen;
    border-top: 1px solid forestgreen;
}

.control-panel-right {
    right: 0;
    text-align: right;
    border-left: 1px solid red;
    border-top: 1px solid red;
}

.control-panel-btn {
    margin-top: 0.5em;
    width: 8em;
    height: 4em;
    outline: none;
    box-shadow: none;
}

.control-panel-btn:disabled {
    font-weight: bold;
    background: whitesmoke;
    color: dodgerblue;
}
